// scss-lint:disable IdSelector
#nprogress {
	pointer-events: none;

	.bar {
		background: $color-2;
		height: 5px;
		left: 0;
		position: fixed;
		top: 0;
		width: 100%;
		z-index: 101;
	}

	.peg {
		display: none;
	}
}

.nprogress-custom-parent {
	overflow: hidden;
	position: relative;

	#nprogress .bar {
		position: absolute;
	}
}
// scss-lint:enable IdSelector
