//  TEXT

%text {
	display: none;
	margin-left: auto;
	margin-right: auto;
	max-width: 800px;

	.wf-active & {
		display: block;
	}

	.wf-active .loadpage & {
		visibility: hidden;
	}

	.wf-active .done & {
		visibility: visible;
	}
}

h1 {
	@extend %text;
	@include text-size(32, 400, 1200, 5);
	font-weight: 700;
	max-width: 1000px;

	.wf-active .loadpage & {
		visibility: visible;
	}
}

h2 {
	@extend %text;
	@include text-size(21, 600, 1200, 30);
	font-weight: 400;
}

h3 {
	@extend %text;
	@include text-size(18, 600, 1200, 0);
	font-weight: 700;
}

p {
	@extend %text;
	@include text-size(15, 600, 800);
	font-weight: 400;

	&.intro {
		@include text-size(21, 600, 800);
		text-align: center;
	}
}

a {
	color: $color-2;
	text-decoration: none;
}

.meta {
	@extend %text;
	@include text-size(15, 600, 800);
	color: shade($color-1, 25);
	font-weight: 400;
	margin: 0 auto;
}
